
import { defineComponent, onMounted, ref } from "vue";
import "ol/ol.css";

import { initMap } from "@/oljs/map";
import {
  createFeature,
  createVecLayer,
  initFeatureClick,
  initFeatureHover,
  destroyHandler,
} from "@/oljs/layer";
import { popupInit, popupShow, popupHide } from "@/oljs/popup";
import {
  initHlSource,
  initHlLayer,
  clearStyle,
  highlight,
} from "@/oljs/areaHighlight";

export default defineComponent({
  components: {},
  setup() {
    let map = null;
    const zoneList = ref([]);
    onMounted(() => {
      map = initMap("openlayerEl");
      const hlSource = initHlSource("/geojson/370000_full.json");
      const hlLayer = initHlLayer(hlSource);
      map.addLayer(hlLayer);
    });

    return { zoneList };
  },
});
