import Fill from "ol/style/Fill";
import GeoJSON from "ol/format/GeoJSON";
import Stroke from "ol/style/Stroke";
import Style from "ol/style/Style";
import VectorSource from "ol/source/Vector";
import { Vector as VectorLayer } from "ol/layer";

//高亮样式
const highlightStyle = new Style({
  fill: new Fill({
    color: "rgba(100, 150, 0,0.2)",
  }),
  stroke: new Stroke({
    color: "#61f2ff",
    width: 3,
  }),
});

//空白样式
const blankStyle = new Style({
  stroke: new Stroke({
    color: "transparent",
  }),
});

//地图source
export function initHlSource(url: string) {
  const source = new VectorSource({
    url: url,
    format: new GeoJSON(),
  });
  return source;
}
export function initHlLayer(source: VectorSource) {
  const layer = new VectorLayer({
    source,
    //   style: blankStyle, //初始化时,去掉默认样式
  });
  return layer;
}

//消除指定source下所有要素的样式,vecSource
export function clearStyle(source: VectorSource): void {
  const featureList = source.getFeatures();
  if (featureList != [] && featureList != null) {
    featureList.forEach((f) => {
      f.setStyle(blankStyle);
    });
  }
}

//触发要素高亮
export function highlight(source: VectorSource, id: string): void {
  clearStyle(source);
  const feature = source.getFeatureById(id);
  if (feature != null) {
    feature.setStyle(highlightStyle);
  }
}
